<template>
  <v-app>
    <v-app>
      <v-main>
        <v-row justify="center">
          <v-col cols="12" sm="6" md="3" lg="4" xl="4">
            <v-img
              :src="mapImage"
              aspect-ratio="7"
              style="opacity: 1; border-radius: 10px; box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.8);"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular indeterminate color="primary"></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="12" sm="12" md="3" lg="2" xl="3">
            <v-autocomplete
              v-model="campaign"
              :items="campaigns"
              item-text="name"
              item-value="name"
              label="Campaign"
              hide-details
              @change="updateScores"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="12" md="3" lg="2" xl="1">
            <v-autocomplete
              v-model="track"
              :items="tracks"
              item-text="name"
              return-object
              label="Track"
              hide-details
              @change="updateScores"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" sm="12" md="3" lg="2" xl="1">
            <v-autocomplete
              v-model="zoneType"
              :items="zoneTypes"
              item-text="name"
              item-value="zoneId"
              label="Zone"
              hide-details
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="12" sm="12" md="9" lg="6" xl="5">
            <v-data-table
              :headers="headers"
              :items="filteredScores"
              :options.sync="pagination"
              :footer-props="{ 'items-per-page-options': [10, 50, 100] }"
              :server-items-length="pagination.totalItems"
              :loading="this.isLoading"
            >
              <template v-slot:[`item.score`]="{ item }">
                <span :style="{ color: getDateDifference(item.timestamp) }">
                  {{ $m(item.timestamp).fromNow() }}
                </span>
                <span style="padding-left: 50px">
                  {{ formatScore(item.recordTime) }}
                </span>
              </template>
            </v-data-table>
            <br />
            <v-tooltip bottom open-delay="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                  icon
                  color="primary"
                  :disabled="track.name === '1'"
                  @click="previousTrack"
                >
                  <v-icon>mdi-arrow-left</v-icon>
                </v-btn>
              </template>
              <span>Previous Track</span>
            </v-tooltip>
            <v-tooltip bottom open-delay="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                  icon
                  color="primary"
                  :disabled="track.name === tracks.length.toString()"
                  @click="nextTrack"
                >
                  <v-icon>mdi-arrow-right</v-icon>
                </v-btn>
              </template>
              <span>Next Track</span>
            </v-tooltip>
          </v-col>
        </v-row>
        <v-tooltip left>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" fixed fab bottom right color="primary" @click="toggleDarkTheme">
              <v-icon v-if="!$vuetify.theme.dark">mdi-lightbulb-outline</v-icon>
              <v-icon v-else>mdi-lightbulb</v-icon>
            </v-btn>
          </template>
          <span>Toggle light/dark theme</span>
        </v-tooltip>
      </v-main>
    </v-app>
  </v-app>
</template>

<script>
import { scaleLinear } from 'd3-scale';

const hourScale = scaleLinear()
  .domain([0, 24, 14 * 24, 2 * 30 * 24])
  .range(['#2eb82e', '#258e25', '#cca300', '#e67300']);

export default {
  name: 'App',
  data() {
    return {
      headers: [
        {
          align: 'start',
          text: 'Player',
          value: 'displayName',
          sortable: false,
        },
        {
          align: 'start',
          text: 'Zone',
          value: 'zoneName',
          sortable: false,
        },
        {
          align: 'end',
          text: 'Time',
          value: 'score',
          sortable: false,
        },
      ],
      scores: [],
      campaigns: [
        {
          name: 'Fall 2020',
          path: 'fall2020',
          route: 'Fall-2020',
          tracks: Array(25)
            .fill()
            .map((_, id) => ({ id: id + 26, name: `${id + 1}` })),
        },
        {
          name: 'Summer 2020',
          path: 'summer2020',
          route: 'Summer-2020',
          tracks: Array(25)
            .fill()
            .map((_, id) => ({ id: id + 1, name: `${id + 1}` })),
        },
      ],
      campaign: 'Fall 2020',
      track: {
        id: 26,
        name: '1',
      },
      isLoading: false,
      zones: [],
      zonesCache: {},
      zoneTypes: [],
      zoneType: {},
      search: '',
      pagination: {
        descending: true,
        page: 1,
        rowsPerPage: 10,
        sortBy: 'score',
        totalItems: 1000,
      },
      abortController: new AbortController(),
    };
  },
  mounted() {
    const paths = document.location.hash.slice(2).split(/\//g);

    if (paths.length === 1) {
      const campaignName = paths[0].toLowerCase();
      const campaign = this.campaigns.find(({ path }) => path === campaignName);

      if (campaign) {
        this.campaign = campaign.name;
      }
    } else if (paths.length === 2) {
      const campaignName = paths[0].toLowerCase();
      const campaign = this.campaigns.find(({ path }) => path === campaignName);

      if (campaign) {
        this.campaign = campaign.name;

        const trackName = paths[1];
        const track = campaign.tracks.find(track => track.name === trackName);
        if (track) {
          this.track = track;
        }
      }
    }

    const zones = 'https://raw.githubusercontent.com/NeKzor/nekzor.github.io/master/trackmania/zones.json';
    fetch(zones)
      .then(res => res.json())
      .then(zones => {
        this.zones = zones.data;

        const world = this.zones.find(zone => zone.parentId === null);

        const continents = this.zones
          .filter(zone => zone.parentId === world.zoneId)
          .map(zone => ({ ...zone, index: 1 }));

        const continentsIds = continents.map(zone => zone.zoneId);

        const countries = this.zones
          .filter(zone => continentsIds.includes(zone.parentId))
          .map(zone => ({ ...zone, index: 2 }));

        this.zoneTypes = [{ ...world, index: 0 }, ...continents, ...countries];
        this.zoneType = world;
      });
  },
  watch: {
    pagination: {
      handler() {
        this.updateScores();
      },
      deep: true,
    },
  },
  computed: {
    filteredScores() {
      return this.scores && this.zoneType
        ? this.scores.filter(score => score.zone.filter(zone => zone.zoneId === this.zoneType.zoneId).length)
        : [];
    },
    tracks() {
      return this.campaigns.find(({ name }) => name === this.campaign).tracks;
    },
    mapImage() {
      const { path } = this.campaigns.find(({ name }) => name === this.campaign);
      return `https://raw.githubusercontent.com/NeKzor/nekzor.github.io/master/trackmania/images/${path}/${this.track.name}.webp`;
    },
  },
  methods: {
    updateScores() {
      const campaign = this.campaigns.find(({ name }) => name === this.campaign);
      if (!campaign) {
        return;
      }

      const leaderboard = 'http://trackmania-api.jonesy.moe/api/map/records/current';
      const limit = this.pagination.itemsPerPage;
      const offset = (this.pagination.page - 1) * (limit + 1);

      const zoneType = {
        world: 0,
        continent: 1,
        country: 2,
        region: 3,
      };

      const url = `${leaderboard}/${this.track.id}?limit=${limit}&offset=${offset}`;
      console.log('[GET] ' + url);

      if (!this.abortController.signal.aborted) {
        this.abortController.abort();
      }

      this.abortController = new AbortController();
      this.isLoading = true;

      fetch(url, { signal: this.abortController.signal })
        .then(res => res.json())
        .then(scores => {
          document.location.hash = `#/${campaign.path}/${this.track.name}`;

          this.scores = scores.map(score => {
            let zone = this.zonesCache[score.nadeoZoneId];
            if (!zone) {
              zone = this.findZone(score.nadeoZoneId);
              this.zonesCache[score.nadeoZoneId] = zone;
            }

            if (!zone[zoneType.country]) {
              console.log('zone?', zone, score);
            }

            return {
              ...score,
              zone,
              zoneName: zone[zoneType.country]
                ? zone[zoneType.country].name
                : zone[zoneType.world]
                ? zone[zoneType.world].name
                : '',
            };
          });
        })
        .catch(console.error)
        .finally(() => (this.isLoading = false));
    },
    findZone(zoneId) {
      const result = [];

      for (const zone of this.zones) {
        if (zone.zoneId === zoneId) {
          if (zone.parentId !== null) {
            result.push(...this.findZone(zone.parentId));
          }
          result.push(zone);
        }
      }

      return result;
    },
    previousTrack() {
      const tracks = this.tracks;
      const curIndex = tracks.indexOf(this.track);
      this.track = tracks[curIndex - 1];
      this.pagination.page = 1;

      this.updateScores();
    },
    nextTrack() {
      const tracks = this.tracks;
      const curIndex = tracks.indexOf(this.track);
      this.track = tracks[curIndex + 1];
      this.pagination.page = 1;

      this.updateScores();
    },
    formatScore(score) {
      const msec = score % 1000;
      const tsec = Math.floor(score / 1000);
      const sec = tsec % 60;
      const min = Math.floor(tsec / 60);

      return (
        (min > 0 ? min + ':' : '') +
        (sec < 10 && min > 0 ? '0' + sec : sec) +
        '.' +
        (msec < 100 ? (msec < 10 ? '00' + msec : '0' + msec) : msec)
      );
    },
    getDateDifference(date) {
      const passedHours = this.$m().diff(this.$m(date), 'hours');
      return passedHours <= 2 * 30 * 24 ? hourScale(passedHours) : undefined;
    },
    toggleDarkTheme() {
      const dark = window.localStorage.getItem('dark-theme') !== 'true';
      window.localStorage.setItem('dark-theme', dark);
      this.$vuetify.theme.dark = dark;
    },
  },
};
</script>
