import Vue from 'vue';
import moment from 'moment';
import App from './App.vue';
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';

Vue.use(Vuetify);

Vue.config.productionTip = false;

Vue.prototype.$m = moment;

new Vue({
  vuetify: new Vuetify(),
  render: h => h(App),
}).$mount('#app');
